/* Vote.css - Using Global Design Tokens */

.Vote-Page {
    background-color: var(--bg-primary);
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 100vh;
    transition: background-color var(--transition-slow);
}

.candidate {
    padding: var(--space-xxl) 0;
}

.candidate h2 {
    color: var(--text-primary);
    margin: var(--space-md) auto;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: var(--font-size-3xl);
    font-weight: var(--font-weight-bold);
}

.Heading1 {
    padding: var(--space-sm);
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: var(--space-xxl);
}

.Heading1 p {
    font-weight: var(--font-weight-bold);
    font-size: var(--font-size-xl);
    color: var(--text-secondary);
    margin: 0;
}

.Heading1 p span {
    font-weight: var(--font-weight-bold);
    color: var(--color-primary);
}

.VoteContent {
    padding: 0;
}

.status-banner {
    padding: var(--space-md);
    margin: var(--space-lg) auto;
    max-width: 800px;
    border-radius: var(--radius-lg);
    text-align: center;
    border: 1px solid var(--border-color);
    box-shadow: var(--shadow-sm);
    transition: all var(--transition-base);
}

.status-banner.active {
    background-color: rgba(76, 206, 172, 0.1);
    border-color: var(--color-success);
}

.status-banner.pending {
    background-color: rgba(104, 112, 250, 0.1);
    border-color: var(--color-primary);
}

.status-banner.ended {
    background-color: rgba(219, 79, 74, 0.1);
    border-color: var(--color-error);
}

.info-banner {
    padding: var(--space-md);
    margin-bottom: var(--space-lg);
    background: var(--grad-surface);
    border-radius: var(--radius-lg);
    border-left: 5px solid var(--color-primary);
    box-shadow: var(--shadow-sm);
}

.glass-modal {
    background: var(--bg-surface) !important;
    backdrop-filter: var(--glass-blur);
    border: 1px solid var(--glass-border) !important;
    border-radius: var(--radius-xl) !important;
    box-shadow: var(--shadow-lg) !important;
}

.VoteGivenBox {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.VoteGivenBox button {
    background: linear-gradient(135deg, var(--color-primary) 0%, var(--color-primary-dark) 100%);
    padding: var(--space-sm) var(--space-xxl);
    margin: var(--space-sm);
    font-size: var(--font-size-md);
    border-radius: var(--radius-full);
    color: white;
    border: none;
    cursor: pointer;
    transition: all var(--transition-base);
    box-shadow: 0 4px 12px var(--button-shadow);
    font-weight: var(--font-weight-semibold);
}

.VoteGivenBox button:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 16px var(--button-shadow);
}

.VoteGivenBox button a {
    color: white;
    text-decoration: none;
    display: block;
    width: 100%;
    height: 100%;
}

@media (max-width: 600px) {
    .candidate h2 {
        font-size: var(--font-size-2xl);
    }
}